import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";

const PersonalCost = () => {
    const [searchValue, setSearchValue] = React.useState();
    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }

    let todayItems = (<>
        <TableRow >
            <TableCell className="padding-sides-25 red text-align-right">$2,00</TableCell>
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}Lionel Messi</span></TableCell>
            <TableCell className="padding-sides-10 color-white">Buy Config</TableCell>
            <TableCell className="padding-sides-20 color-white">Purchased config
                <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.cog} Chrome 78 on Windows 10 </span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white text-align-right">$987,00</TableCell>
            <TableCell className="padding-sides-15 color-grey">3 Apr, 19:20:55</TableCell>
        </TableRow>

        <TableRow >
            <TableCell className="padding-sides-25 green text-align-right">$1000,00</TableCell>
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}Lionel Messi</span></TableCell>
            <TableCell className="padding-sides-10 color-white">Buy Config</TableCell>
            <TableCell className="padding-sides-20 color-white">Buy Corporate License
                <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.license} 1234567890</span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white text-align-right">$1991</TableCell>
            <TableCell className="padding-sides-15 color-grey">3 Apr, 19:20:55</TableCell>
        </TableRow>

        <TableRow >
            <TableCell className="padding-sides-25 red text-align-right">$2,00</TableCell>
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}Lionel Messi</span></TableCell>
            <TableCell className="padding-sides-10 color-white">Using browser</TableCell>
            <TableCell className="padding-sides-20 color-white">Pay per day using the browser
                <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.calendar} 25.02.2020</span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white text-align-right">$991,00</TableCell>
            <TableCell className="padding-sides-15 color-grey">3 Apr, 19:20:55</TableCell>
        </TableRow>

    </>);


    let todaySection = (<>
        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">Today</b>
                <span className="color-grey inline">April 3</span>
            </TableCell>
        </TableRow>
        {todayItems}

    </>);

    let yesterdaySection = (<>
        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">Yesterday</b>
                <span className="color-grey inline">April 2</span>
            </TableCell>

        </TableRow>
        {todayItems}
    </>);

    /* active */
    return (
        <div className="сustomStyle">
            <div className="filterPanel">

                <TextField
                    variant="filled"
                    fullWidth
                    className="search_area"
                    placeholder="Search"
                    value={searchValue}
                    onChange={(e)=>{setSearchValue(e.target.value)}}
                >


                </TextField>
                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                    <div>{IconPack.searchComponents.filter}</div>
                </div>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-25 w125 text-align-right">Amount</TableCell>
                        <TableCell className="padding-sides-15 w150">User</TableCell>
                        <TableCell className="padding-sides-10 w120">Cost action</TableCell>
                        <TableCell className="padding-sides-20 ">Cost detail</TableCell>
                        <TableCell className="padding-sides-20 w125 text-align-right">Bought left</TableCell>
                        <TableCell className="padding-sides-15 w125">Date</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {todaySection}
                    {yesterdaySection}
                </TableBody>
            </Table>
        </div>
    );
}
export default PersonalCost;